.usuarios {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.usuariosContent {
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 0;
}

.topBar {
  position: sticky;
  top: 0;
  z-index: 10;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  gap: 12px;
  padding: 8px 0;
  background: var(--gray-50);
  border-bottom: 1px solid var(--gray-200);
  margin-bottom: 12px;
}

.topBarActions {
  display: flex;
  align-items: center;
  gap: 8px;
}

.refreshBtn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border: 1px solid var(--gray-200);
  background: var(--white);
  border-radius: var(--radius-md);
  font-family: var(--font-body);
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--gray-700);
  cursor: pointer;
  transition: all 0.2s;
}

.refreshBtn:hover:not(:disabled) {
  border-color: var(--azure);
  color: var(--azure);
}

.refreshBtn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.createBtn {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  border: none;
  background: var(--azure);
  color: var(--white);
  border-radius: var(--radius-md);
  font-family: var(--font-body);
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.createBtn:hover {
  background: var(--azure-dark);
}

.tabContent {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  min-height: 0;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.spinning {
  animation: spin 1s linear infinite;
}

